import React from "react";
import "./type-and-delete.css";

interface TypeAndDeleteProps {
  textArr: string[];
}

let i = 0;
let index = 0;

export const TypeAndDelete: React.FC<TypeAndDeleteProps> = ({
  textArr,
}: TypeAndDeleteProps) => {
  const [text, setText] = React.useState("");

  React.useEffect(() => {
    setInterval(() => {
      setText(textArr[i].substr(0, index));
      index++;
      if (index > textArr[i].length) {
        i++;
        i %= textArr.length;
        index = 0;
      }
    }, 200);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="typeAndDeleteComp">
      <div className="text">{text}</div>
      <div className="cursor"></div>
    </div>
  );
};
